import { computed, defineComponent, ref } from '@nuxtjs/composition-api';
export default defineComponent({
    name: 'SearchInput',
    fetchOnServer: true,
    inheritAttrs: false,
    props: {
        searchFields: {
            type: Array,
            required: true
        }
    },
    setup(props) {
        const selectedSearchField = ref(props.searchFields[0]);
        const overwriteAttrs = computed(() => {
            return {};
        });
        const defaultAttrs = computed(() => {
            return {
                'prepend-inner-icon': '$magnify',
                clearable: true,
                dense: true,
                filled: true,
                outlined: true,
                placeholder: selectedSearchField.value + '...'
            };
        });
        const commonListeners = computed(() => {
            return {};
        });
        return {
            selectedSearchField,
            defaultAttrs,
            overwriteAttrs,
            commonListeners
        };
    }
});
